const DetailsForm = ({
  _handleValue,
  addProduct,
  pname,
  oprice,
  sprice,
  ptype,
  description
}) => {
  return (
    <div className="component_wrapper">
      <input
        type="text"
        placeholder="Product Name"
        value={pname}
        className="text_input"
        onChange={(e) => _handleValue(e.target.value, "pname")}
      />
      <input
        type="text"
        placeholder="Original Price"
        value={oprice}
        className="text_input"
        onChange={(e) => _handleValue(e.target.value, "oprice")}
      />
      <input
        type="text"
        placeholder="Sale Price"
        value={sprice}
        className="text_input"
        onChange={(e) => _handleValue(e.target.value, "sprice")}
      />
      <input
        type="text"
        placeholder="Product Type"
        value={ptype}
        className="text_input"
        onChange={(e) => _handleValue(e.target.value, "ptype")}
      />
      <textarea
        type="text"
        placeholder="Description"
        className="description_area"
        value={description}
        rows="4"
        onChange={(e) => _handleValue(e.target.value, "description")}
      />
      <button className="btn_primary" onClick={addProduct}>
        Create
      </button>
    </div>
  );
};

export default DetailsForm;
