import { useEffect, useState } from "react";
import axios from "axios";
import CustomeTable from "./Components/CustomeTable";
import DetailsForm from "./Components/DetailsForm";
import "./Styles/index.scss";

export default function App() {
  const [data, setData] = useState("");
  const [status, setStatus] = useState("");
  const [pname, setPname] = useState("");
  const [oprice, setOprice] = useState("");
  const [sprice, setSprice] = useState("");
  const [ptype, setPtype] = useState("");
  const [description, setDescription] = useState("");

  useEffect(() => {
    setPname("");
    setOprice("");
    setSprice("");
    setPtype("");
    setDescription("");
    const getCall = async () => {
      await fetch("https://lobster-app-ddwng.ondigitalocean.app/product/list", {
        headers: { api_key: "Z9Q7WKEY7ORGBUFGN3EG1QS5Y7FG8DU29GHKKSZH" }
      })
        .then((response) => response.json())
        .then((res) => setData(res.message));
    };

    if (status === 200 || status === "") {
      getCall();
    }
  }, [status]);

  const _handleValue = (value, type) => {
    switch (type) {
      case "pname":
        setPname(value);
        return;
      case "oprice":
        setOprice(value);
        return;
      case "sprice":
        setSprice(value);
        return;
      case "ptype":
        setPtype(value);
        return;
      case "description":
        setDescription(value);
        return;
      default:
        return;
    }
  };

  const addProduct = async () => {
    const tempProd = {
      product_name: pname,
      original_price: oprice,
      sale_price: sprice,
      product_type: ptype,
      description: description
    };
    try {
      await axios
        .post(
          "https://lobster-app-ddwng.ondigitalocean.app/product/add_new",
          tempProd,
          {
            headers: { api_key: "Z9Q7WKEY7ORGBUFGN3EG1QS5Y7FG8DU29GHKKSZH" }
          }
        )
        .then((res) => {
          setStatus(res.status);
        });
    } catch (error) {
      alert("Got error", error);
    }
  };

  return (
    <div className="App">
      <div className="container">
        <h1>Ultimez Technology</h1>
        <h2>Create New Product</h2>
        <DetailsForm
          addProduct={addProduct}
          _handleValue={_handleValue}
          pname={pname}
          oprice={oprice}
          sprice={sprice}
          ptype={ptype}
          description={description}
        />
        <CustomeTable data={data} />
      </div>
    </div>
  );
}
